<template>
  <div class="second">
     <div class="engine" v-for="(item,index) in equipmentRunList" :key="index">
        <!-- <div class="equName">{{item.EQUIPMENTNAME}}</div>
        <div class="time">{{item.EQUIPMENTCODE}}</div> -->
        <div class="left">
            <div style="position:absolute;z-index:9999;margin-left:0.8%;margin-top:1.2%;">{{index+1}}</div>
            <i class="fa fa-microchip" v-bind:style="{'color':(item.STATUS == '0'?'#8c8c8c':'#13c2c2')}"></i>
        </div>
         <div class="right">
            <!-- <div class="date">今日</div>  -->
            <div class="run">运行</div>
            <div class="date">{{item.RUNTIME}}h</div>
            <div class="stop">异常</div>
            <div class="date">{{item.DOWNTIME}}</div>
         </div>
     </div>
  </div>
</template>

<script>
export default {
    name: 'Second',
    data () {
        return {                      
            equipmentRunList: [],
            intervalId:"",
			url:""
        }
    
    },
    methods:{
        getDetailData(){
            var self = this;
            self.equipmentRunList.forEach(function(item,index,array){
                self.equipmentRunList[index].RUNTIME = Math.round((item.RUNTIME/3600));
                self.equipmentRunList[index].DOWNTIME = Math.round((item.DOWNTIME/3600));
                return self.equipmentRunList;          
            })
        },
        getNewData(){
		this.url ='http://' + window.location.host;
		console.log(this.url);
      	this.$axios.get(`${this.url}/dafeng_mes/equipmentrun2/equipmentOperation.do`)
        .then(res => {
            console.log(res); 
            this.equipmentRunList = res.data.equipmentRunList
            this.getDetailData();         
        });
        },
        },
        created(){
            this.getNewData();
            this.intervalId = setInterval(() => {
                this.getNewData()
            },600000)                                           //定时刷新  10min
        },
        beforeDestroy () {
            clearInterval(this.intervalId)
        }
}
</script>


<style scoped>
*{
    margin: 0;
    padding: 0;
}
.equName{
    color: #fff;
}
.second{
    width: 100%;
    height: 600px;
    overflow: hidden;
    text-align: center;
    margin-top: 1%;
}
.engine{
    width: 110px;
    height: 90px;
    border: 1px rgba(238, 238, 238, 0.4)  solid;
    border-radius: 5px;
    /* background: rgba(0, 0, 102, 0.2) ; */
    float: left;
    margin: 3px;
}
.left{
    float: left;
    padding-top: 25px;
    padding-left: 10px;
}
.right{
    float: left;
    padding-top: 5px;
    padding-left: 10px;
}
.run{
    color: #a0d911;
}
.date{
    color: #fff;
}
.time{
    color: #fadb14;
}
.stop{
    color: #ff4d4f;
}
.runIcon{
    font-size:48px;
    color:#87e8de;    
}
.stopIcon{
    font-size:48px;
    color:#ff4d4f;
}
.engineColor{
    font-size:48px;
    color:#87e8de;   
}
.fa{
    font-size: 48px;
}
</style>
 


